.EquipmentList{
  padding:10px 30px 10px 30px;
  border-color:silver;
  border-style:solid;
  border-radius:10px;
  margin-bottom:10px;
}

.ffxiEquipBox{
  background-color:#F2F2F2;
  border-color: rgb(47,56,55);
  border-width:2px;
  border-style:solid;
  padding-left:2px;
  padding-right:2px;
  padding:0px;
  margin:0 auto;
}

.ffxiButton{
  width:48%;
  height:20px;
  font-size:12px;
  line-height:1px;
}


.ffxiEquipPicture{
  max-width:64px;
  max-height:64px;
  margin:0 auto;
  width:32px;
  height:32px;
}

.imageCont{
  height:64px;
  padding:16px;
  width:64px;
  margin: 0 auto;
  vertical-align: -webkit-baseline-middle;
}

.statsPanel{
  color:white;
  background-color:#333;
  border-radius:20px;
  border-width:1px;
  border-style:solid;
  border-color:white;
}

.spacedRow{
  margin-bottom:10px;
}

.ffxiEquipBox{
  background-color: #657b98;
  border-top-color: #31496a;
  border-left-color: #31496a;
  border-bottom-color: #7086a8;
  border-right-color: #8ba2bf;
  border-style:solid;
  height:100%;
  border-width:8px;
}
.ffxiInput{
  width:90%;
  height:20px;
  font-size:small;
}

.itemName{
  margin-bottom:20px;
  color: white;
  font-weight:400;
  font-size:18px;
}

.inputBox{
  position: relative;
  bottom: 0;
  width:100%;
}

.footer{
  position:fixed;
  bottom:0;
  height:25px;
  color:white;
  background-color:#333;
  width:100%;
  border-top-style:solid;
  border-width:1px;
  border-top-color:black;
}

.footer span{
  margin:0 auto;
}
